import Routers from './routers'
import { connect } from 'react-redux'
import { FuncDispatch, UserInfo } from '@/entities'

function mapState(state: any) {
  const strSearch = state.router.location.search.substr(1)
  let search = {}

  if (strSearch) {
    search = strSearch.split('&').reduce((pre: any, item: string) => {
      const [key, value] = item.split('=')
      pre[key] = value || true
      return pre
    }, {})
  }
  return {
    search,
    userInfo: state.userInfo,
  }
}

function mapDispatch(dispatch: FuncDispatch) {
  return {
    commitUserInfo(userInfo: UserInfo): void {
      dispatch({ type: 'USER_INFO', payload: userInfo })
    }
  }
}

export default connect(mapState, mapDispatch)(Routers)
