import request, { Method } from './request'
import { toProto, buf2str } from '../helper/proto'
import { SignInForm, UserInfo } from './packet_pb'
import md5 from 'md5'
import { URI, encodeBase64 } from '@/utils'

const AUTH_ADDR = 'https://shopping.watsonserve.com/api/auth.html'

interface SignInParams {
  userName: string
  password: string
  salt: string
  sign: string
}

export function loadUserInfo() {
  const foo = (~~(Math.random() * 10000000)).toString(16)
  let salt = '';
  return encodeBase64(foo)
  .then((_salt: string) => {
    salt = _salt;
    return request({ url: '/api/user-info.pb', method: Method.GET })
  })
  .then((respData: any) => {
    debugger
    const userInfo = UserInfo.deserializeBinary(respData.data).toObject()
    if (!userInfo.token) {
      return userInfo;
    }
    const { token, app } = userInfo
    const redirect = `${AUTH_ADDR}?rd=${encodeURIComponent(window.location.pathname)}`
    const signal = md5(app + token + salt + redirect)
    const params = URI.encode({app, token, salt, redirect, signal})
    window.location.href = `https://passport.watsonserve.com/?${params}`
  })
}

export function loadAppInfo(appId: string) {
  return request({ url: `/api/app-base-info.pb?app=${appId}`, method: Method.GET })
  .then(respData => buf2str(respData.data))
}

export function signIn(reqBody: SignInParams) {
  return request({ url: '/api/sign-in.pb', data: toProto(new SignInForm(), reqBody) })
}

export function signUp(reqBody: SignInParams) {
  return request({ url: '/api/sign-up.pb', data: toProto(new SignInForm(), reqBody) })
}

export function sendMail(mail: string) {
  mail = encodeURIComponent(mail)
  return request({ url: '/api/send-mail', method: Method.GET, headers: { 'X-Mail': mail } })
}
