import React from 'react'
import t from '@/helper/i18n'
import Nav from '@/components/nav'
import OrderCard from './order'
import './index.styl'
import { IOrder, IOrderSet, OrderSetProcess, ILogistics, LogisticsCmp } from '@/entities'

interface BookingProps {
  value: IOrderSet[]
}

const OrderStatMap = {
  [OrderSetProcess.DISPATCHING]: t('order-dispatching'),
  [OrderSetProcess.CANCEL]: t('order-cancel'),
  [OrderSetProcess.REJECT]: t('order-reject'),
  [OrderSetProcess.TRANSITING]: t('order-transiting'),
  [OrderSetProcess.RECEIVED]: t('order-received'),
}

export default class Booking extends React.Component<BookingProps, any> {
  renderGoodItem(item: IOrder) {
    return (
      <li className="ordre-list__item">
        <OrderCard value={item} />
      </li>
    )
  }

  renderOrders(orderSet: IOrderSet) {
    const {
      orders,
      hssPayment,
      logistics,
      reciver,
      status,
    } = orderSet;
    const {
      logisticsCmp,
      serialNO,
      freightPrice,
    } = logistics || {} as ILogistics;

    return (
      <dl className="order-set">
        <dt className="order-set__info">
          <span className="status">{ OrderStatMap[status] }</span>
          {serialNO && <div className="logistics-info">
            <span>SN:</span>
            <p>{ serialNO }</p>
          </div>}
        </dt>
        <dd className="order-set__list">
          <ul className="ordre-list">
            { orders.length && orders.map(item => this.renderGoodItem(item)) }
          </ul>
        </dd>
      </dl>
    )
  }

  render() {
    const ordersList = this.props.value

    return (
      <Nav className="booking">
        { ordersList && ordersList.map(item => this.renderOrders(item)) }
      </Nav>
    )
  }
}
