import React from 'react'
import { boundMethod } from 'autobind-decorator'
// 组件
import { Link } from 'react-router-dom'
import Nav from '@/components/nav'
import Picture from '@/components/picture'
// 数据类型, 接口
import t from '@/helper/i18n'
import { UserInfo } from '@/entities'
// style
import './mine.styl'

interface MineProps {
  userInfo: UserInfo | null
  commitUserInfo: (userInfo: UserInfo) => void
}

export default class Mine extends React.Component<MineProps, any> {

  @boundMethod
  handleSignOut(): void {
    // @TODO
  }

  render() {
    const { name, avatar } = this.props.userInfo || {
      name: 'James',
      avatar: '',
    } as UserInfo;

    return (
      <Nav className="mine-page">
        <section className="base-info">
          <Picture className="avatar" src={avatar} title={name} />
          <span className="aka">{ name }</span>
        </section>
        <ul className="mine-list">
          <li className="mine-list__item">
            <Link className="link" to="/address">{ t('address') }</Link>
          </li>
          <li className="mine-list__item">
            <Link className="link" to="/about">{ t('about') }</Link>
          </li>
        </ul>
        <a href="javascript:;" className="btn large primary sign-out" onClick={this.handleSignOut}>{ t('sign-out') }</a>
      </Nav>
    )
  }
}

