export interface Action {
  type: string
  payload: any
}

export type FuncDispatch = (action: Action) => void

export class Mail {
  private name: string
  private domain: string

  constructor(email: string) {
    const [name, domain] = email.split('@')
    this.name = name
    this.domain = domain
  }

  toString() {
    return `${this.name}@${this.domain}`
  }
}

export interface UserInfo {
  name: string
  avatar: string
  phone: string
  mail: Mail
  address: string
}

export enum Currency {
  CNY = 'CNY',
  EUR = 'EUR',
  HKD = 'HKD',
  JPY = 'JPY',
  KRW = 'KRW',
  MYR = 'MYR',
  RUB = 'RUB',
  TWD = 'TWD',
  USD = 'USD',
}

export enum Region {
  INTERNATIONAL = '🇺🇳',
  FRA = '🇫🇷',
  GBR = '🇬🇧',
  HKG = '🇭🇰',
  JPN = '🇯🇵',
  KOR = '🇰🇷',
  PRC = '🇨🇳',
  TWN = '🇹🇼',
  RUS = '🇷🇺',
  USA = '🇺🇸',
}

// 物品
export interface IThing {
  pic: string
  name: string
  aka: string
  desc: string
  unit: string
  version: string[]
  origin: Region
}

// 钱
export class IMoney {
  private much: number
  private currency: Currency
  constructor(much: number, currency: Currency) {
    this.much = much
    this.currency = currency
  }
  toString(): string {
    return this.currency + ` ${this.much}`
  }
}

// 商品
export interface IGood extends IThing {
  maxUnitPrice: IMoney
  unitPrice: IMoney
}

export enum OrderProcess {
  WAITTING,
  CANCEL,
  FINDING,
  GOT,
  REJECT,
}

// 订单
export interface IOrder extends IGood {
  count: number
  status: OrderProcess
}

export enum LogisticsCmp {
  EMS = 'EMS',
  JD = 'JD', // 京东
  SF = 'SF', // 顺丰
  ST = 'ST', // 申通
  YD = 'YD', // 韵达
  YT = 'YT', // 圆通
  YZ = 'YZ', // 邮政
  ZT = 'ZT', // 中通
}

export interface ILogistics {
  logisticsCmp: LogisticsCmp
  serialNO: string
  freightPrice: IMoney
}

export interface IReciver {
  name: string
  phone: string
  address: string
  zipCode: number
}

export enum OrderSetProcess {
  DISPATCHING,
  CANCEL,
  REJECT,
  TRANSITING,
  RECEIVED,
}

// 可物流订单集
export interface IOrderSet {
  orders: IOrder[]
  hssPayment: boolean
  logistics?: ILogistics
  reciver: IReciver
  status: OrderSetProcess
}
