import React from 'react'
import t from '@/helper/i18n'

import Picture from '@/components/picture'

import './index.styl'
import { IOrder } from '@/entities'

interface OrderProps {
  value: IOrder
}

export default class Order extends React.Component<OrderProps, any> {
  render() {
    const {
      count,
      status,
      pic,
      origin,
      name,
      aka,
      unit,
      version,
      maxUnitPrice,
      unitPrice,
    } = this.props.value;

    const goodName = `${name} -- ${aka}`
    let price = maxUnitPrice
    let priceLabel = t('max-unit-price')
    if (unitPrice) {
      price = unitPrice
      priceLabel = t('unit-price')
    }

    return (
      <div className="order-card flex">
        <Picture className="good-img" src={pic} title={aka} titleLen={aka.length} />
        <div className="order-info">
          <h4 className="good-name">{ origin + ' ' + goodName }</h4>
          <p className="price-info">
            <span className="unit-price">{ priceLabel + ' ' + price.toString() }</span>
            <span className="count pull-right">{ `${version} * ${count} ${unit}` }</span>
          </p>
          <span className="order-stat">{ status }</span>
        </div>
      </div>
    )
  }
}
