import React from 'react'
import { NavLink } from 'react-router-dom'
import t from '@/helper/i18n'
import './nav.styl'

type NavProps = {
  className?: string
  router: any
}

export default class Nav extends React.Component<NavProps> {
  render() {
    const { children } = this.props
    let className = this.props.className
    if (className && className.trim()) {
      className = `nav-frame ${className}`
    }

    return (
      <main className={className}>
        <div className="container">
          { children }
        </div>
        <nav className="main-nav">
          <ul className="main-nav__list">
            <li className="main-nav__item">
              <NavLink className="main-nav__btn" replace to="/found">
                <i className="icon icon-found" />
                <span>{ t('found') }</span>
              </NavLink>
            </li>
            <li className="main-nav__item">
              <NavLink className="main-nav__btn" replace exact to="/">
                <i className="icon icon-found" />
                <span>{ t('booking') }</span>
              </NavLink>
            </li>
            <li className="main-nav__item">
              <NavLink className="main-nav__btn" replace to="/mine">
                <i className="icon icon-mine" />
                <span>{ t('mine') }</span>
              </NavLink>
            </li>
          </ul>
        </nav>
      </main>
    )
  }
}
