import React, { Component } from 'react'
import { BrowserRouter, Switch, Route } from 'react-router-dom'
import { Spin } from 'antd'

import { loadUserInfo } from '@/api'

const ErrorView = React.lazy(() => import('@/pages/errors'))
const Booking = React.lazy(() => import('@/pages/booking'))
const Found = React.lazy(() => import('@/pages/found'))
const Mine = React.lazy(() => import('@/pages/mine'))
const About = React.lazy(() => import('@/pages/about'))

export default class Routers extends Component<any, any> {
  constructor(props: any) {
    super(props)

    this.state = {
      loaded: 1,
    }

    // this._loadAuthInfo()
  }

  private _loadAuthInfo(): void {
    loadUserInfo()
    .then((userInfo) => {
      this.props.commitUserInfo(userInfo)
      this.setState({ loaded: 1 })
    }).catch(err => {
      this.setState({ loaded: -1 })
    })
  }

  render() {
    const loaded = this.state.loaded
    switch (loaded) {
      case 0:
        return <Spin />
      case -1:
        return <div className="container-wrapper">
          <ErrorView />
        </div>
    }
    return (
      <div className="container-wrapper">
        <BrowserRouter>
          <Switch>
            <Route path="/found" component={Found} />
            <Route path="/mine" component={Mine} />
            <Route path="/" exact component={Booking} />
            <Route path="/about" component={About} />
            <Route title="Page Not Found" component={ErrorView} />
          </Switch>
        </BrowserRouter>
      </div>
    )
  }
}
